.testemonials {
  @include page-grid;

  margin-bottom: 90px;

  @include on-tablet {
    margin-bottom: 118px;
  }

  @include on-small-desktop {
    margin-bottom: 126px;
  }

  @include on-desktop {
    margin-bottom: 118px;
  }

  &__header {
    grid-column: 1 /-1;
  }

  &__mark {
    color: #c0cdd7;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__title {
    margin: 16px 0 48px;
    color: #253757;
    font-family: Poppins, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; /* 48px */

    @include on-tablet {
      width: 190px;
    }
  }

  &__card {
    grid-column: 1 /-1;
    margin-bottom: 24px;
    padding: 36px 42px 32px;
    background: #fff;
    border-radius: 16px;
    transition-duration: 0.3s;

    &:hover {
      transform: scale(1.05);
      box-shadow: 5px 5px 5px #95a1ac;
      transition-duration: 0.3s;
    }

    @include on-tablet {
      margin-bottom: 30px;
      padding: 36px 98px 32px;
    }

    @include on-small-desktop {
      padding: 36px 48px 32px;
    }

    @include on-desktop {
      padding: 36px 56px 32px;
    }
  }

  &__card-1 {
    @include on-small-desktop {
      grid-column: 1 /5;
    }
  }

  &__card-2 {
    @include on-small-desktop {
      grid-column: 5 /9;
    }
  }

  &__card-3 {
    @include on-small-desktop {
      grid-column: 9 /-1;
    }
  }

  &__card-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__card-photo {
    width: 110px;
    height: 110px;
    border-radius: 50%;
    overflow: hidden;

    @include on-tablet {
      width: 140px;
      height: 140px;
    }

    @include on-small-desktop {
      width: 110px;
      height: 110px;
    }
  }

  &__photo-wrapper {
    position: relative;
    margin: 40px 0 67px;
    z-index: 1;

    @include on-tablet {
      margin: 52px 0 79px;
    }

    @include on-small-desktop {
      margin: 40px 0 67px;
    }
  }

  &__photo-wrapper::before {
    content: '';
    display: block;
    position: absolute;
    left: -36px;
    top: 0;
    transform: rotate(-60deg);
    width: 110px;
    height: 44px;
    z-index: -1;
    border-radius: 12px;
    background: #2060f6;

    @include on-tablet {
      width: 139px;
      height: 56px;
    }

    @include on-small-desktop {
      width: 110px;
      height: 44px;
    }
  }

  &__photo-wrapper::after {
    content: '';
    display: block;
    position: absolute;
    right: -36px;
    bottom: 0;
    transform: rotate(-60deg);
    width: 110px;
    height: 44px;
    z-index: -1;
    border-radius: 12px;
    background: #2060f6;

    @include on-tablet {
      width: 139px;
      height: 56px;
    }

    @include on-small-desktop {
      width: 110px;
      height: 44px;
    }
  }

  &__card-avatar-1 {
    width: 100%;
  }

  &__card-avatar-2, &__card-avatar-3 {
    height: 100%;
    margin-left: -26px;
  }

  &__card-quote {
    background: url('../images/testimonials/quote.png');
    display: block;
    width: 24px;
    height: 24px;
  }

  &__card-text {
    margin: 8px 0 16px;
    color: #6c788b;
    text-align: center;
    font-family: 'Open Sans',sans-serif;
    font-size: 13px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; /* 19.5px */
  }

  &__card-name {
    color: #253757;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    font-size: 13px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; /* 19.5px */
  }

  &__card-job {
    margin-top: 4px;
    color: #c0cdd7;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 3px;
    text-transform: uppercase;
  }
}
