.process {
  @include page-grid;

  margin-bottom: 90px;

  @include on-tablet {
    margin-bottom: 118px;
  }

  &__content {
    grid-column: 1 /-1;

    @include on-tablet {
      grid-column: span 3;
    }

    @include on-small-desktop {
      grid-column: span 4;
      margin-top: -40px;
    }
  }

  &__card {
    grid-column: 1 /-1;
    margin-bottom: 24px;
    padding: 32px;
    border-radius: 16px;
    background: #fff;
    box-shadow: 0px 9px 18px 0px rgba(37, 41, 49, 0.03);
    transition-duration: 0.3s;

    @include on-tablet {
      margin-bottom: 30px;
    }
  }

  &__card-1, &__card-3 {
    @include on-tablet {
      grid-column: 1 / 4;
    }

    @include on-small-desktop {
      grid-column: 7 /10;
    }

    &:hover {
      transform: scale(1.05);
      box-shadow: 5px 5px 5px #95a1ac;
      transition-duration: 0.3s;
    }
  }

  &__card-2, &__card-4 {
    @include on-tablet {
      grid-column: 4 /-1;
      transform: translateY(-50%);
    }

    @include on-small-desktop {
      grid-column: 10 /-1;
    }
  }

  &__mark {
    color: #c0cdd7;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__title {
    margin: 16px 0 0;
    color: var(--Text-100, #253757);
    font-family: Poppins, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 130%; /* 41.6px */
  }

  &__description {
    margin: 24px 0 48px;
    padding-right: 22px;
    color: #6c788B;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 21px */

    @include on-tablet {
      margin: 24px 0 56px;
    }
  }

  &__card-options {
    margin: 26px 0 32px;
    color: #6c788b;
    font-family: 'Open Sans', sans-serif;
    font-size: 13px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; /* 19.5px */
  }

  &__link {
    display: inline-block;
    margin-bottom: 12px;
    text-decoration: none;
    color: var(--Text-200, #334563);
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    cursor: pointer;
    transition-duration: 0.3s;

    &:hover {
      transform: scale(1.05);
      transition-duration: 0.3s;
    }

    &::before {
      content: '';
      display: inline-block;
      margin-bottom: -15px;
      margin-right: 18px;
      width: 40px;
      height: 40px;
      background-image: url("../images/sub-title/Arrow.png");
    }
  }
}

.wrapper-process {
  @include on-small-desktop {
    padding-top: 128px;
  }
}
