html {
  scroll-behavior: smooth;
  margin: 0;
}

.page {
  margin: 0;
  scroll-behavior: smooth;
}

.buttons {
  width: 280px;
  padding: 14px 0;
  border: none;
  border-radius: 8px;
  background: #2060f6;
  color: #fff;
  text-align: center;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  cursor: pointer;
  transition-duration: 0.3s;

  &:hover {
    background: #36a3fc;
    transition-duration: 0.3s;
  }
}

.container-black--top {
  background: #2c2c2c;
  border-radius: 0 0 30px 30px;
}

.container-black--bottom {
  margin-top: 120px;
  background: #2c2c2c;
  border-radius: 30px 30px 0 0;

  @include on-tablet {
    margin-top: -56px;
  }

  @include on-small-desktop {
    margin-top: -200px;
  }
}

.container-white {
  margin: 120px 0;
  background: #fff;
  border-radius: 30px;

  @include on-tablet {
    margin: 148px 0;
  }
}

.bottom-block {
  @include page-grid;
}
