.expertise {
  padding-bottom: 70px;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;

  @include on-tablet {
    padding-bottom: 128px;
  }

  @include on-small-desktop {
    padding-bottom: 134px;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }

  &__card {
    margin-top: 56px;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    transition-duration: 0.3s;

    &:hover {
      transition-duration: .3s;
      transform: scale(1.05);
      box-shadow: 5px 5px 5px #95a1ac;
    }

    @include on-tablet {
      margin-top: 64px;
      width: 65%;
    }

    @include on-small-desktop {
      width: 22%;
    }
  }

  &__photo {
    width: 184px;
    height: 166px;
  }

  &__foto {
    width: 100%;
    height: 100%;
    border-radius: 20px;
  }
  &__main-title {
    margin: 70px 0 0;
    color: #253757;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 44px;
    font-style: normal;
    font-weight: 600;
    line-height: 120%; /* 52.8px */

    @include on-tablet {
      margin: 128px 0 0;
      font-size: 52px;
      line-height: 150%;
    }

    @include on-small-desktop {
      flex-basis: 100%;
    }
  }

  &__title {
    margin: 32px 0 16px;
    color: #253757;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%; /* 28px */

    @include on-small-desktop {
      margin: 40px 0 16px;
    }
  }

  &__description {
    margin: 0;
    color: #6c788B;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 21px */
  }
}
