.we-are {
  @include page-grid;

  &__content {
    grid-column: 1 /-1;
    margin-top: 120px;

    @include on-tablet {
      margin-top: 148px;
    }

    @include on-small-desktop {
      grid-column: 2 /12;
    }

    @include on-desktop {
      grid-column: 3 /11;
    }
  }

  &__title {
    margin: 0;
    color: #253757;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; /* 48px */
  }

  &__text {
    margin: 0 auto;
    padding-top: 24px;
    width: 280px;
    color: #6c788B;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; /* 36px */

    @include on-tablet {
      margin: 16px 0 0;
      width: 100%;
    }
  }
}
