.contacts {
  grid-column: 1 /-1;

  @include on-small-desktop {
    grid-column: 7 /-1;
  }

  @include on-desktop {
    grid-column: 8 /-1;
  }

  &__title {
    margin: 0 0 48px;
    color: #fff;
    font-family: Poppins, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; /* 48px */

    @include on-tablet {
      margin: 0 0 56px;
    }
  }

  &__visit {
    color: #fff;
    font-family: Poppins, sans-serif;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 3px;
    text-transform: uppercase;
    opacity: 0.5;
  }

  &__number {
    display: block;
    margin: 8 0 40px;
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 36px */
    text-decoration: none;
    

    &:hover {
      color: #2060f6;
      transition-duration: 0.3s;
    }

    @include on-tablet {
      margin: 8 0 48px;
    }
  }

  &__adress {
    margin: 8 0 40px;
    display: block;
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 36px */
    text-decoration: none;
    transition-duration: 0.3s;

    &:hover {
      color: #2060f6;
      transition-duration: 0.3s;
    }

    @include on-tablet {
      margin: 8 0 48px;
    }
  }

  &__icons {
    margin-top: 16px;
  }

  &__link {
    display: inline-block;
    width: 32px;
    height: 32px;
    margin-right: 32px;
    text-decoration: none;
  }

  &__facebook {
    background: url('../images/contacts/facebook 1.png');
    transition-duration: 0.3s;

    &:hover {
      background: url('../images/contacts/facebook-blue.png');
      transform: scale(1.2);
      transition-duration: 0.3s;
    }
  }

  &__twitter {
    background: url('../images/contacts/twitter\ 1.png');
    transition-duration: 0.3s;

    &:hover {
      background: url('../images/contacts/twitter-blue.png');
      transform: scale(1.2);
      transition-duration: 0.3s;
    }
  }

  &__instagram {
    background: url('../images/contacts/instagram.png');
    transition-duration: 0.3s;

    &:hover {
      background: url('../images/contacts/instagram-blue.png');
      transition-duration: 0.3s;
    }
  }
}
