.wrapper-white {
  background-color: #fff;
  background-image: url(../images/Shapes.png);
  background-repeat: no-repeat;
  background-position: center center;
}

.vision {
  @include page-grid;

  text-align: center;

  &__content {
    grid-column: 1 /-1;

    @include on-small-desktop {
      grid-column: 2 /12;
    }

    @include on-desktop {
      grid-column: 3 /11;
    }
  }

  &__title {
    margin: 72px 0 24px;
    color: #253757;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 44px;
    font-style: normal;
    font-weight: 600;
    line-height: 120%; /* 52.8px */

    @include on-tablet {
      margin: 200px 0 24px;
      font-size: 52px;
    }

    @include on-small-desktop {
      line-height: 150%;
    }
  }

  &__text {
    margin: 0 0 56px;
    color: #6c788b;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; /* 33px */

    @include on-tablet {
      margin: 0 0 64px;
      font-size: 24px;
    }
  }

  &__button {
    margin-bottom: 72px;

    @include on-tablet {
      margin-bottom: 256px;
    }

    @include on-small-desktop {
      margin-bottom: 390px;
    }
  }

  &__link {
    color: #fff;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-decoration: none;
  }
}
