.strategic {
  @include page-grid;

  &__content {
    grid-column: 1 /-1;

    @include on-small-desktop {
      grid-column: 1 /6;
    }
  }

  &__title {
    margin: 48px 0 32px;
    color: #fff;
    font-family: Poppins, sans-serif;
    font-size: 52px;
    font-style: normal;
    font-weight: 600;
    line-height: 120%;

    @include on-tablet {
      margin: 56px 0 24px;
      font-size: 64px;
      line-height: 68px;
    }

    @include on-small-desktop {
      margin: 160px 0 32px;
    }
  }

  &__text {
    margin-bottom: 70px;
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 27px */

    @include on-tablet {
      margin-bottom: 56px;
    }
  }

  &__button {
    text-align: center;

    @include on-tablet {
      text-align: left;
    }
  }

  &__link {
    color: #fff;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-decoration: none;
  }
}

.strategic-button.buttons {
  grid-column: 1 /-1;
  margin-bottom: 48px;
  width: 100%;

  @include on-tablet {
    margin-bottom: 148px;
    width: 280px;
  }
}

.slider {
  position: relative;
  grid-column: 1 /-1;
  margin-inline: -20px;

  @include on-tablet {
    margin-inline: -40px;
  }

  @include on-small-desktop {
    grid-column: 7 /-1;
    margin: 40px -54px 0 0;
  }

  @include on-desktop {
    grid-column: 8 /-1;
    margin: 40px -160px 0 0;
  }

  @include on-design {
    margin: 40px -225px 0 0;
  }

  @include on-large-screen {
    margin: 40px -705px 0 0;
  }

  &__photo {
      width: 100%;
      height: 100%;
      border-radius: 30px;
      object-fit: cover;

    @include on-small-desktop {
      border-radius: 30px 0 30px 0;
    }
  }

  &__arrows {
    position: absolute;
    bottom: 24px;
    margin-left: 20px;

    @include on-tablet {
      margin-left: 40px;
      bottom: 32px;
    }

    @include on-small-desktop {
      display: flex;
      align-items: center;
      margin-left: 78px;
      bottom: 48px;
    }

    @include on-desktop {
      margin-left: 48px;
    }
  }

  &__arrow-left {
    margin-right: 16px;
    transition-duration: 0.3s;

    &:hover {
      transform: scale(1.2);
      transition-duration: 0.3s;
    }
  }

  &__arrow-right {
    transition-duration: 0.3s;

    &:hover {
      transform: scale(1.2);
      transition-duration: 0.3s;
    }

    @include on-small-desktop {
      margin-right: 24px;
    }
  }

  &__description {
    width: 280px;
  }

  &__intro {
    margin: 16px 0 8px;
    color: #fff;
    font-family: Poppins, sans-serif;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 3px;
    text-transform: uppercase;

    @include on-small-desktop {
      margin: 8px 0;
    }
  }

  &__text {
    margin: 0;
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    font-style: normal;
    letter-spacing: 1.5px;
    font-weight: 400;
    line-height: 140%; /* 19.6px */
  }
}
