.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px ;
  height: 36px;

  @include on-small-desktop {
    padding-top: 48px;
  }

  &__icon {
    margin-bottom: 48px;

    @include on-tablet {
      margin-bottom: 24px;
    }

    @include on-small-desktop {
      margin: 0;
    }
  }

  &__logo {
    padding-right: 32px;
  }

  &__icon-logo {
    transition-duration: 0.3;

    &:hover {
      transform: scale(1.1);
      transition-duration: 0.3s;
    }
  }

  &__menu {
    @include on-small-desktop {
      display: none;
    }
  }

  &__links {
    display: none;

    @include on-small-desktop {
      display: block;
    }
  }

  &__link {
    margin-left: 42px;
    padding-bottom: 8px;
    text-decoration: none;
    color: #fff;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: 150%; /* 19.5px */
    letter-spacing: 3px;
    text-transform: uppercase;
    transition-duration: 0.3s;

    &:hover {
      border-bottom: 4px solid #2060f6;
      border-radius: 4px;
      width: fit-content;
      transition-duration: 0.3s;
    }
  }

  &__link-here {
    padding: 8px 0;
    margin-left: 48px;
  }
}

.container-black {
  position: relative;
}

.slider {
  right: 0;
  bottom: 0;
}
