.footer {
  padding-bottom: 60px;

  @include on-tablet {
    padding-bottom: 80px;
  }

  &__nav {
    display: flex;
    flex-direction: column;

    @include on-tablet {
      justify-content: space-between;
    }

    @include on-small-desktop {
      flex-direction: row;
    }
  }

  &__links {
    display: flex;
    flex-direction: column;

    @include on-tablet {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  }

  &__link {
    margin-bottom: 24px;
    color: #fff;
    font-family: Poppins, sans-serif;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: 150%; /* 19.5px */
    letter-spacing: 3px;
    text-transform: uppercase;
    text-decoration: none;
    transition-duration: 0.3s;

    @include on-tablet {
      margin: 0;
    }

    &:hover {
      color: #2060f6;
      transition-duration: 0.3s;
    }

    @include on-small-desktop {
      margin-left: 64px;
    }
  }
}
