.message {
  grid-column: 1 /-1;

  @include on-tablet {
    grid-column: span 4;
  }

  @include on-small-desktop {
    grid-column: span 5;
  }

  &__title {
    margin:0 0 7px;
    color: #fff;
    font-family: Poppins, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 130%; /* 41.6px */

    @include on-tablet {
      line-height: 150%;
    }
  }

  &__inputs {
    margin-top: 40px;

    @include on-tablet {
      margin-top: 48px;
    }
  }

  &__input {
    width: 100%;
    height: 40px;
    box-sizing: border-box;
    background: #2c2c2c;
    color: #fff;
    border: none;
    border-bottom: 1px solid #fff;
    outline: none;
    cursor: pointer;

    &::placeholder {
      color: #fff;
      font-family: Poppins, sans-serif;
      font-size: 15px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      opacity: 0.7;
    }

    &:hover {
      border-bottom: 2px solid #144ed4;
    }
  }

  &__textarea {
    resize: none;
  }

  &__button {
    margin: 56px 0 80px;
    text-align: center;

    @include on-tablet {
      margin: 80px 0 120px;
      text-align: left;
    }
  }
}

.bottom-block {
  padding: 72px 0 80px;

  @include on-tablet {
    padding: 180px 0 140px;
  }

  @include on-small-desktop {
    padding-bottom: 120px;
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus {
  border: 1px solid #0296f8;
  border-radius: 6px;
  -webkit-text-fill-color: #0296f8;
  -webkit-box-shadow: 0 0 0px 1000px #000 inset;
  transition: background-color 5000s ease-in-out 0s;
}
